<template>
  <div class="views">
    <div class="title">
      <mytil></mytil>
    </div>
    <div class="content">
      <router-view></router-view>
    </div>
    <div class="nav">
      <cube-tab-bar
      v-model="selectedLabelDefault"
      :data="tabs"
      @click="clickHandler"
      @change="changeHandler"
      :showSlider='true'
      :useTransition='true'>
       <cube-tab v-for="(item) in tabs" :label="item.label" :key="item.label">
      <!-- name为icon的插槽 -->
      <i slot="icon" :class="item.icon"></i>
      <!-- 默认插槽 -->
      <div>{{item.label}}</div>
    </cube-tab>
    </cube-tab-bar>
    </div>
  </div>
</template> 
<script>
  export default {
  data () {
    return {
      selectedLabelDefault: '首页',
      tabs: [{
        label: '首页',
        icon: 'cubeic-home'
      }, {
        label: '书架',
        icon: 'cubeic-calendar'
      }, {
        label: '购物车',
        icon: 'cubeic-mall'
      }, {
        label: '我的',
        icon: 'cubeic-person'
      }]
    }
  },
  created() {
    this.selectedLabelDefault = this.$store.state.page
  },
  methods: {
    clickHandler (label) {
      // if you clicked home tab, then print 'Home'
      console.log(label)
    },
    changeHandler (label) {
      // if you clicked different tab, this methods can be emitted
       switch (label) {
        case "首页":
          this.$router.push("/home");
          break;
        case "书架":
          this.$router.push("/list");
          break;
        case "购物车":
          this.$router.push("/collect");
          break;
        case "我的":
          this.$router.push("/person");
          break;
      }
    }
  }
}
</script>
<style lang="less" scoped>
.views {
  height:100%;
  display: flex;
  flex-direction: column;
  .title {
    width: 100%;
    height: 1rem;
    flex: 0;
  }
  .content {
    flex: 1;
    width: 100%;
    overflow:auto;
    margin-top: 1rem;
  }
  .nav {
    flex:0;
    width: 100%;
    font-size: 20px;
    height: 2rem;
    box-shadow: 0 1px 0 0 rgba(238,238,238,.5), 0 0 0.1rem 0 rgba(0,0,0,.18);
    align-items: center;
    background: white;
    z-index: 99;
    
  }
}

</style>
